import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './FourPhotos.css';

function FourPhotos(props){
    return(
        <Container style={{maxWidth: "100vw"}}>
            <Row>
                <Col lg id="destination-container">
                    <Row>
                        <Col lg>
                            <div className="card" id="destination-card">
                                <img src={props.image1} className="card-img-top" alt="placeholder" style={{border: "5rem solid white"}} />
                                <div className="card-body">
                                    <p className="card-text">{props.text1}</p>
                                </div>
                            </div>
                        </Col>
                        <Col lg>
                            <div className="card" id="destination-card">
                                <img src={props.image2} className="card-img-top" alt="placeholder" style={{border: "5rem solid white"}} />
                                <div className="card-body">
                                    <p className="card-text">{props.text2}</p>
                                </div>
                            </div>
                        </Col>
                    </Row>
                    <Row>
                        <Col lg>
                            <div className="card" id="destination-card">
                                <img src={props.image3} className="card-img-top" alt="placeholder" style={{border: "5rem solid white"}} />
                                <div className="card-body">
                                    <p className="card-text">{props.text3}</p>
                                </div>
                            </div>
                        </Col>
                        <Col lg>
                            <div className="card" id="destination-card">
                                <img src={props.image4} className="card-img-top" alt="placeholder" style={{border: "5rem solid white"}} />
                                <div className="card-body">
                                    <p className="card-text">{props.text4}</p>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </Container>
    )
}

export default FourPhotos;