const nolight3 = () => {
    document.querySelector(".combo").style.textDecoration = "none";
    setTimeout(() => {
        highlight3();
    }, 1000)
}

const highlight3 = () => {
    document.querySelector(".combo").style.textDecoration = "underline wavy red";
    setTimeout(() => {
        nolight3();
    }, 1000)
}

function Combo(props){
    setTimeout(() => highlight3(), 1000);
    return(
    <div className="combo-container">
        <h4>{props.header}</h4>
        {props.header2}
    </div> 
    )
}

export default Combo;