const ContactForm = (props) => {
    const [form, setForm] = useState(true);
    const [success, setSuccess] = useState(false);
    const [error, setError] = useState(false);

    return(
        <div className="form-container">
            <h2 className="contact-header">Contact Us</h2>
            <div className="contact-inner">
                {form &&
                    <Formik 
                        initialValues={{
                            firstName: '',
                            lastName: '',
                            email: '',
                            destination: '',
                            message: ''
                        }}
                    validationSchema={Yup.object({
                        firstName: Yup.string()
                            .max(20, "Must Be 20 Characters Or Less")
                            .required("Required")
                            .matches(/^[a-zA-Z]+$/, "Can only be letters"),
                        lastName: Yup.string()
                            .max(20, "Must Be 20 Characters Or Less")
                            .required("Required")
                            .matches(/^[a-zA-Z]+$/, "Can only be letters"),
                        email: Yup.string()
                            .email("Invalid Email")
                            .required("Required"),
                        destinations: Yup.string()
                            .oneOf(
                                ["Disney World (Orlando)", "Disneyland (California)", "Disney Cruise Lines", "Aulani Spa and Resort"],
                                "Invalid Destination"
                            ),
                        message: Yup.string()
                                .max(500, "Must Be 500 Characters Or Less"),
                    })}
                    onSubmit={(values, { setSubmitting }) => {
                        setTimeout(() => {
                            setForm(false);
                            setSuccess(true);
                            alert(JSON.stringify(values, null, 2));
                            setSubmitting(false);
                        }, 400);
                    }}
                    >
                    <Form>
                        <MyTextInput
                            label="First Name:"
                            name="firstName"
                            type="text"
                            placeholder="First Name"
                        />
                        <br />
                        <MyTextInput 
                            label="Last Name:"
                            name="lastName"
                            type="text"
                            placeholder="Last Name"
                        />
                        <br />
                        <MyTextInput 
                            label="Email:"
                            name="email"
                            type="email"
                            placeholder="Email Address"
                        />
                        <br />
                        <MySelect label="Destination:" name="destination">
                            <option value="">Select A Destination</option>
                            <option value="Disney World (Orlando)">Disney World (Orlando)</option>
                            <option value="Disneyland (California)">Disneyland (California)</option>
                            <option value="Disney Cruise Line">Disney Cruise Line</option>
                            <option value="Aulani Spa and Resort">Aulani Spa and Resort</option>
                        </MySelect>
                        <br />
                        <MyTextInput
                            label="Message:"
                            name="message"
                            type="message"
                            placeholder="Additional Info, Questions, Comments, etc."
                        />
                        <br />
                        <button className="contact-submit" type="submit"><h4>Submit</h4></button>
                    </Form>
                </Formik>
            }
            {success && 
                <div className="success-container">
                    <div className="success-message">
                        <h3>Thank you!</h3>
                        <p>We'll Be In Touch Soon</p>
                    </div>
                </div>
            }
            {error && 
                <div className="error-container">
                    <div className="error-message">
                        <h3>Uh-oh!</h3>
                        <p>Something went wrong, please try again</p>
                    </div>
                </div>
            }
        </div>
    </div>
  )
}

export default ContactForm;