import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './Steps.css';

function Steps(props){
    return(
        <Container>
            <Row>
                <Col lg>
                    <div className="gears">{props.gears}</div>
                </Col>
                <Col lg>
                    <table className="gears">
                            <tbody>
                                <tr>
                                    <td>
                                        <p className="step">{props.label1}</p>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <p className="step">{props.label2}</p>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <p className="step">{props.label3}</p>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <p className="step">{props.label4}</p>
                                    </td>
                                </tr>
                            </tbody>
                        </table> 
                </Col>
            </Row>
        </Container>
    )
}

export default Steps;