const nolight2 = () => {
    document.querySelector(".twenty-five").style.textDecoration = "none";
    setTimeout(() => highlight2(), 1000)
}

const highlight2 = () => {
    document.querySelector(".twenty-five").style.textDecoration = "underline wavy #ffe866";
    setTimeout(() => nolight2(), 1000)
}

function VipDeal(props) {
    setTimeout(() => highlight2(), 1000);
    return(
        <div className="vip-message">
            {props.header}
            <p>{props.text}</p>
        </div>
    )
}

export default VipDeal;