import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './TripleCard.css';

function TripleCard(props){
    return(
        <Container fluid>
            <Row>
                <Col lg id="tri-fold">
                    <div style={{height: "15rem"}}>{props.image1}</div>
                    <p id="tri-fold-label-1">{props.header1}</p>
                    <p className="tri-fold-bottom">{props.pitch1}</p>
                </Col>
                <Col lg id="tri-fold">
                    <div style={{height: "15rem"}}>{props.image2}</div>
                    <p id="tri-fold-label-2">{props.header2}</p>
                    <p className="tri-fold-bottom">{props.pitch2}</p>
                </Col>
                <Col lg id="tri-fold">
                    <div style={{height: "15rem"}}>{props.image3}</div>
                    <p id="tri-fold-label-3">{props.header3}</p>
                    <p className="tri-fold-bottom">{props.pitch3}</p>
                </Col>
            </Row>
        </Container>
    )
}

export default TripleCard;