import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './Perks.css'

function Perks(props){
    return(
      <Container fluid>
          <Row>
              <Col lg>
                <div className="perks">
                    <h3>{props.header1}</h3>
                    <p>{props.pitch1}</p>
                </div>
              </Col>
              <Col lg>
                    <div style={{textAlign: "center"}} className="perks">{props.image1}</div>
              </Col>
          </Row>
          <Row>
              <Col lg>
                <div className="perks">
                    <div style={{textAlign: "center", height: "27em", marginTop: "3em"}}>
                        {props.image2}
                    </div>
                    <h3>{props.header2}</h3>
                    <p>{props.pitch2}</p>
                </div>
              </Col>
              <Col lg>
                <div className="perks">
                <h3>{props.header3}</h3>
                <p>{props.pitch3}</p>
                <div style={{textAlign: "center", padding: "3em"}}>{props.image3}</div>
                </div>
              </Col>
          </Row>
      </Container>  
    )
}

export default Perks;