import { HashRouter, Routes, Route } from "react-router-dom";
import About from './pages/About.js';
import Contact from './pages/Contact.js';
import Home from './pages/Home.js';
import NavBar from './pages/NavBar.js';
import DisneyWorld from "./pages/DisneyWorld.js";
import Disneyland from "./pages/Disneyland.js";
import DisneyCruise from "./pages/DisneyCruise.js";
import Aulani from "./pages/Aulani.js";
import Footer from "./pages/Footer.js";
import NotFoundError from "./NotFoundError.js";

function App() {
  return (
    <>
      <HashRouter>
        <NavBar />
        <Routes>
            <Route index element={<Home />} />
            <Route path="/about" element={<About />} />
            <Route path="/contact" element={<Contact />} />
            <Route path="/walt-disney-world" element={<DisneyWorld />} />
            <Route path="/disneyland" element={<Disneyland />} />
            <Route path="/disney-cruise-line" element={<DisneyCruise />} />
            <Route path="/aulani-spa-resort" element={<Aulani />} />
            <Route path="/not-found-error" element={<NotFoundError />} />
        </Routes>
      </HashRouter>
      <Footer />
    </>
  );
}

export default App;
