import { Container, Row, Col } from 'react-bootstrap';
import './SpinningGear.css';

function SpinningGear(){
    return(
        <div>
            <div>
                <hr className="black-line" />
                <hr className="red-line" />
                <hr className="black-line" />
                <hr className="yellow-line" />
                <hr className="black-line" />
                <hr className="yellow-line" />
                <hr className="black-line" />
                <hr className="red-line" />
                <hr className="yellow-line" />
            </div>
            <Container className="gear-container" fluid>
                <Row>
                    <Col>
                        <img id="small-gear" className="rotate" src="./images/gear.png" />
                    </Col>
                    <Col>
                        <img id="small-gear" className="rotate" src="./images/gear.png" />
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <img id="big-gear" className="rotate" src="./images/gear.png" />
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default SpinningGear;