import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './FinalPitch.css';

function FinalPitch(props){
    return(
        <Container style={{maxWidth: "100vw"}}>
            <Row>
                <Col lg>
                    <h3 className="tagline">{props.tag}</h3>
                    <button onClick={() => window.location.href = "#/contact"} className="final-pitch">{props.buttonTitle}</button>
                </Col>
            </Row>
        </Container>
    )
}

export default FinalPitch;