import { Outlet, Link } from "react-router-dom";
import { Nav, Navbar, NavDropdown, Container } from "react-bootstrap";
import { LinkContainer } from 'react-router-bootstrap';
import './NavBar.css';

function NavBar() {
    return (
        <div>
            <Navbar expand="lg">
                <Container className="nav-container">
                <LinkContainer to="/"><Navbar.Brand><img className="logo" src="./images/logo-resize.png" /></Navbar.Brand></LinkContainer>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                    <div className="center-nav">
                        <Nav className="me-auto">
                            <LinkContainer to="/about">
                                <Nav.Link id="link">About Us</Nav.Link>
                            </LinkContainer>
                            <LinkContainer to="/contact">
                                <Nav.Link id="link">Contact Us</Nav.Link>
                            </LinkContainer>
                            <NavDropdown title="Our Destinations" id="basic-nav-dropdown">
                                <LinkContainer to="/walt-disney-world">
                                    <NavDropdown.Item id="link">Walt Disney World</NavDropdown.Item>
                                </LinkContainer>
                                <LinkContainer to="/disneyland">
                                    <NavDropdown.Item id="link">Disneyland</NavDropdown.Item>
                                </LinkContainer>
                                <LinkContainer to="/disney-cruise-line">
                                    <NavDropdown.Item id="link">Disney Cruise Line</NavDropdown.Item>
                                </LinkContainer>
                                <LinkContainer to="/aulani-spa-resort">
                                    <NavDropdown.Item id="link">Aulani Spa and Resort</NavDropdown.Item>
                                </LinkContainer>
                            </NavDropdown>
                        </Nav>
                    </div>
                    </Navbar.Collapse>
                </Container>
                <button onClick={() => window.location.href = "#/contact"} className="consultation">Book Your Free Consultation</button>
            </Navbar>
            <Outlet />
        </div>
    )
}

export default NavBar;