import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

const SevenTenRightText = (props) => {
    return(
        <Container className="seven-ten">
            <Row>
                <Col lg={{span: 6, order: 1}}>
                    <h1 style={{textAlign: "center"}}>Placeholder</h1>
                </Col>
                <Col lg={{span: 6, order: 2}}>
                    <p>{props.header}</p>
                    <p>{props.text}</p>
                </Col>
            </Row>
        </Container>
    )
}

export default SevenTenRightText;