import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './ThreeFifths.css';

function ThreeFifths(props){
    return (
        <>
            <Container fluid>
                <Row>
                    <Col className="parent-row" lg={7}>
                        <div className="pitch">
                            <h1>{props.header}</h1>
                            <hr style={{backgroundColor: "transparent"}} />
                            <p className="messenger-ship">{props.text}</p>
                            <hr style={{backgroundColor: "transparent"}} />    
                        </div>
                        {props.button &&
                            <button onClick={() => window.location.href = "#/contact"} className="pitch-button">{props.buttonLabel}</button>
                        }
                    </Col>
                    <Col lg={5}>
                        <p style={{textAlign: "center"}}>{props.photoGallery}</p>
                    </Col>
                </Row> 
            </Container>
        </>
    )
}

export default ThreeFifths;