import ContactForm from '../components/ContactForm'
import HalfAndHalf from '../components/HalfAndHalf';
import { useState, useEffect } from 'react';
import './Contact.css';
import './NavBar.css';

const ContactPitch = () => {
    return(
        <div className="pitch-container">
            <h2 className="header">
                Are You Ready for the Ultimate Disney Adventure?
            </h2>
            <div className="spacer1"></div>
            <p>
                You're just a few steps away from creating an adventure you'll never forget.
            </p>
            <div className="spacer2"></div>
            <p>
                All you have to do is fill out the form, let us know if you have a specific destination in mind and if you have any questions.
            </p>
            <div className="spacer2"></div>
            <p>
                We'll be in touch within 24 hours to take care of the rest!
            </p>
        </div>
    )
}

function Contact(){
    useEffect(() => {
        console.log("hit");
        document.querySelector(".consultation").style.display = "none";
        return () => {
            document.querySelector(".consultation").style.display = "block";
        }
    })
    return(
        <HalfAndHalf 
            element1={<ContactPitch />}
            element2={<ContactForm />}
        />
    )
}

export default Contact;