const nolight = () => {
    document.querySelector(".two-eighty-nine").style.textDecoration = "none";
    setTimeout(() => highlight(), 1000)
}

const highlight = () => {
    document.querySelector(".two-eighty-nine").style.textDecoration = "underline wavy red";
    setTimeout(() => nolight(), 1000)
}

function Concierge(props){
    setTimeout(() => highlight(), 1000);
    return(
        <div className="concierge-message">
            <h4>{props.text}</h4>
            {props.text2}
        </div>
    )
}

export default Concierge;