import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

const CheckCard = (props) => {
    return(
        <Container className="check-card-container">
            <Row>
                <Col lg={3}>
                    <img src="./images/checkmark.png" alt="Check" className="check-mark" />
                </Col>
                <Col lg>
                    <h4>{props.header}</h4>
                    <p>{props.text}</p>
                </Col>
            </Row>
        </Container>
    )
}

export default CheckCard;