import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

function HalfAndHalf(props){
    return(
        <Container>
            <Row>
                <Col lg>
                    {props.element1}
                </Col>
                <Col lg>
                    {props.element2}
                </Col>
            </Row>
        </Container>
    )
}

export default HalfAndHalf;