import "./Home.css";
import ThreeFifths from "../components/ThreeFifths";
import Perks from "../components/Perks";
import TripleCard from "../components/TripleCard";
import Steps from "../components/Steps";
import FourPhotos from "../components/FourPhotos";
import FinalPitch from "../components/FinalPitch";
import SpinningGear from "../components/SpinningGear";
import NavBarAlt from "../pages/NavBarAlt";

function Home(){
    return (
        <div>
            <ThreeFifths
                header="Your Disney Adventure of a Lifetime is about to begin!"
                text="Have you been dreaming of the ultimate Disney Vacation? Feeling overwhelmed by the limitless possibilities? Here at Mousetermind Travel, we specialize in creating custom, once-in-a-lifetime experiences at the Disney parks and beyond, so your family can share in creating lifelong memories without the hassle of booking or budgeting for your trip. Take the stress out of planning and come see what we have to offer today!"
                buttonLabel="Book Your Free Consultation"
                photoGallery="Photo Gallery"
                onClick={() => window.location.pathname = "./mousetermindtravel/contact"}
                button={true}
            />
            <Perks 
                header1="At Mousetermind Travel, we have a mind for Disney!"
                pitch1="Planning a Disney vacation looks complicated, but it doesnt have to be! You decide which experiences mean the most to you and your family, and we design a custom itinerary for you, complete with booking, reservation options, and sharing our best insider tips, so you can relax knowing your perfect vacation is waiting for you!"
                image1="Placeholder"
                image2="Placeholder"
                header2="What is YOUR dream Disney adventure?"
                pitch2="Do you daydream of lounging on the beach, soaking up the sun and the sand of Disneys Aulani Resort in Hawaii? Or maybe you imagine yourself strolling down Main Street, USA, with the head Mouse himself! Whatever it is you dream of doing, were here to make it happen! Our years of Disney vacation planning expertise saves you time and money, so you have more to spend on what really matters most- the memories that will last you a lifetime."
                header3="More magic, Less stress!"
                pitch3="Do you want more out of your vacation, with less hassle? We have options for every vacation type to help you save time and money, and packages that will fit into almost any budget!"
                image3="Placeholder"
            />
            <TripleCard 
                image1="Placeholder"
                header1="Personalized Recommendations"
                pitch1="Your Maousetermind Travel agent will grant your familys every wish. No call centers or impersonal one size fits all advicethis is a vacation built from the ground up just for you and your loved ones."
                image2="Placeholder"
                header2="Little Known Tips & Insider Secrets"
                pitch2="Imagine the memories youll make when you ask our die-hard Disney fans to plan for you. Youll save 30+ hours of planning timeand get access to all of our first-hand experience and genie-level tips and tricks."
                image3="Placeholder"
                header3="More Magic In Less Time"
                pitch3="Our only goal is to ensure your dream vacation is as magical and memorable as possible. So well show you exactly how to have double or even triple the funand one-of-a-kind experiences you cant get anywhere else."
            />
            <Steps 
                gears={<SpinningGear />}
                label1="Step 1"
                label2="Step 2"
                label3="Step 3"
                label4="Step 4"
            />
            <FourPhotos 
                image1="./images/logo-resize.png"
                text1="Walt Disney World (Orlando)"
                image2="./images/logo-resize.png"
                text2="Disneyland (California)"
                image3="./images/logo-resize.png"
                text3="Disney Cruise"
                image4="./images/logo-resize.png"
                text4="Aulani Spa and Resort"
            />
            <FinalPitch 
                tag="Ready for the dream vacation of a lifetime?"
                buttonTitle="GET STARTED WITH A FREE, NO-OBLIGATION CONSULTATION"
                onClick={() => window.location.pathname = "./mousetermindtravel/contact"}
            />
        </div>
    )
}

export default Home;